import React, { useEffect, useState } from "react";
import "./Header.css";
import axios from "axios";

function Header() {
  const [songs, setSongs] = useState([]);
  useEffect(() => {
    axios
      .get("http://localhost:1337/api/v1/headerSongs")
      .then((response) => setSongs(response.data));
  });
  return (
    <div className="header">
      {songs.map((song) => {
        return (
          <div className="header-tile">
            <div className="tile-img" style={{backgroundImage:`url("${song.imgURL}")`}}></div>
            <div className="tile-content">{song.name}</div>
          </div>
        );
      })}
    </div>
  );
}

export default Header;
