import React from "react";
import "./Footer.css";

function Footer() {
  return (
    <div className="footer">
      <div className="player-container">
        <div className="song-container">
          <div className="player-img"></div>
          <div>
              <h1 className="player-heading">Malang</h1>
              <p className="player-sub-heading">Pritam</p>
          </div>
        </div>
        <div className="player-icon-container">
          <i class="fas fa-desktop player-icon"></i>
          <i class="far fa-heart player-icon"></i>
          <i class="fas fa-play player-icon"></i>
        </div>
      </div>
      <div className="bottom-nav-icon-container">
        <i class="fas fa-home bottom-nav-icon"></i>
        <i class="fas fa-search bottom-nav-icon"></i>
        <i class="fas fa-book-open bottom-nav-icon"></i>
      </div>
    </div>
  );
}

export default Footer;
